<template>
  <div class="base_table">
  <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-s-order"></i> 基础表格
                </el-breadcrumb-item>
  </el-breadcrumb>
  </div>
  <div>
    <el-table
      ref="filterTable"
      :data="tableData"
      style="width: 100%">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
     <el-table-column
      prop="id"
      label="ID"
      width="180">
    </el-table-column>
      <el-table-column
        prop="date"
        label="注册时间"
        sortable
        width="180"
        column-key="date"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址"
        :formatter="formatter">
      </el-table-column>
      <el-table-column
        prop="tag"
        label="标签"
        width="100"
        :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.tag === '家' ? 'primary' : 'success'"
            disable-transitions>{{scope.row.tag}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
    <span class="footer_text">共{{tableData.length}}条</span>
    <el-pagination
    class="pagination"
      background
      layout="prev, pager, next"
      :total="1">
    </el-pagination>
    <!-- 编辑弹出框 -->
    <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="30%">
  <el-form >
    <el-form-item label="用户名" >
      <el-input  autocomplete="off" v-model="from.name"></el-input>
    </el-form-item>
    <el-form-item label="地址">
      <el-input  autocomplete="off"  v-model="from.address"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="saveEdit">确 定</el-button>
  </div>
</el-dialog>
  </div>
</div>

</template>

<script>
export default {
  name: 'baseTable',
  data () {
    return {
       dialogFormVisible:false,
       tableDateIndex: 0,
       from: {
         name: '',
         address: ''
       },
       tableData: [{
          id: '1008611',
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          tag: '家'
        }, {
          id: '1008612',
          date: '2017-08-04',
          name: '张三',
          address: '广州市番禺区汀沙村 15号',
          tag: '公司'
        }, {
          id: '1008613',
          date: '2018-03-01',
          name: '李四',
          address: '茂名市电白区滨海路221号',
          tag: '家'
        }, {
          id: '1008614',
          date: '2019-011-03',
          name: '王五',
          address: '重庆市巫溪镇',
          tag: '公司'
        }]
    }
  },
   methods: {
      formatter(row) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      },
      handleEdit(index, row) {
        console.log(index, row);
        this.from.name = this.tableData[index].name
        this.from.address = this.tableData[index].address
        this.tableDateIndex = index
        this.dialogFormVisible = true
      },
      handleDelete(index) {
        this.$confirm('是否确定删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.tableData.splice(index,1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
    saveEdit () {
    this.tableData[this.tableDateIndex].name = this.from.name
    this.tableData[this.tableDateIndex].address = this.from.address
    this.dialogFormVisible = false
    this.$message({
            type: 'success',
            message: '修改成功!'
      });
    }
  }
}
</script>

<style scoped>
  .base_table {
    position: relative;
    padding: 20px;
    min-width: 1000px;
  }
  .pagination {
    position: absolute;
    bottom: -36px;
    right: 20px;
  }
  .footer_text {
    position: absolute;
    bottom: -30px;
    right: 146px;
    margin: 20px 0;
    font-size: 16px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
</style>